html, body, .root {
  height: 100%;
}

body {
  line-height: 1.5;
}

body, .app {
  background: white;
}

/* main page */
.list-books-title {
  padding: 0px 0;
  background-image: -webkit-gradient(linear, left top, right bottom, from(#5edbdb), to(#47bad3));
  background-image: -webkit-linear-gradient(top left, #5edbdb, #47bad3);
  background-image: -o-linear-gradient(top left, #5edbdb, #47bad3);
  background-image: linear-gradient(to bottom right, #5edbdb, #47bad3);
  text-align: center;
  font-size: 40px;
  font-family: 'Amatic SC', sans-serif;
  letter-spacing: .2rem;
}

.list-books-title h1 {
  font-weight: 200;
  margin: 0;
  color: white;
  padding: 0;
}

.list-books-content {
  padding: 0 0 10px;
  -ms-flex: 1 1;
      flex: 1 1;
}

.bookshelf {
  padding: 0 10px 20px;
}

@media (min-width: 600px) {
  .bookshelf {
    padding: 0 10px 5px;
  }
}

.bookshelf-title {
  border-bottom: 1px solid rgba(49, 171, 204, 0.59);
  text-align: center;
  font-family: 'Petit Formal Script', cursive;
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 20pt;
  font-weight: 200;
}

.bookshelf-books {
  text-align: center;
}

.open-search {
  position: fixed;
  right: 25px;
  bottom: 25px;
}

.open-search a {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #5edbdb;
  background-image: url('./icons/add.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 28px;
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  font-size: 0;
}

/* search page */
.search-books-bar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-shadow: 0 10px 20px rgba(28, 210, 235, 0.19), 0 0 6px rgba(84, 207, 210, 0.44);
          box-shadow: 0 10px 20px rgba(28, 210, 235, 0.19), 0 0 6px rgba(84, 207, 210, 0.44);
}

.search-books-input-wrapper {
  -ms-flex: 1 1;
      flex: 1 1;
  background: #9aecee;
  padding-left: 2px;
}

.search-books-bar input {
  width: 100%;
  padding: 15px 10px;
  font-size: 1.25em;
  border: none;
  outline: none;
  font-family: 'Petit Formal Script', cursive;
}

.close-search {
  display: block;
  top: 20px;
  left: 15px;
  width: 50px;
  height: 53px;
  background: #fff;
  background-image: url('./icons/arrow-back.svg');
  color: #9aecee;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 28px;
  font-size: 0;
}

.search-books-results {
  padding: 80px 10px 20px;
}

.searchError {
  text-align: center;
  color: #47bad3;
  font-family: 'Petit Formal Script';
}

/* books grid */
.books-grid {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.books-grid li {
  padding: 10px 15px;
  text-align: center;
}

.book {
  width: 140px;
}

.book-title {
  margin-top: 10px;
  font-size: 14pt;
  text-transform: uppercase;
}

.book-authors {
  color: #717171;
  font-size: 11pt;
  text-transform: uppercase;
}

.book-top {
  position: relative;
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
}

.book-shelf-changer {
  position: absolute;
  right: 0;
  bottom: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #47bad3;
  background-image: url('./icons/arrow-drop-down.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.book-shelf-changer select {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

/* book cover */
.book-cover {
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background: #eee;
}

.book-cover-title {
  padding: 20px 10px 0;
  text-align: center;
  font-size: 0.8em;
}


